import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Pt2H = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Пятница(1ПИН)[2 неделя]</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 пара │ [12:20 – 13:55] – Этика деловых отношений │ (пр)доц.НИКОЛАЕВА О.А. │ Кабинет 307
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 4 пара │ [14:25 - 16:00] – Тайм-менеджмент │ (пр) ст.пр.КТРЕЕВА М.М. │ Кабинет 307
                </Text>
            </Div>
        </Group>
        <Group header={<Header mode="secondary"> </Header>}>

            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pinh2">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Pt2H;