import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const BtE = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Вторник(1Э)</PanelHeader>


        <Group>
            <Div>

                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 Пара | [8:30 - 10:05] - Математический анализ | доц. Туртин Д.В. | дистант |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 - 11:50] - Линейная алгебра | доц. Анисимова И.В. | дистант |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 - 13:55] - Английский язык | доц. Егошина Н.Б. | дистант |
                </Text>
            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="e1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default BtE;