import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Pt = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Пятница(1M)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤  1 Пара | [8:30 - 10:05] – Право | ст.пр. Пантелеев О.В. | каб. 304 | / 1н
                    Макроэкономика | доц. Голышева Е.Е. | каб. 304/ 2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 – 11:50] – Теория организации | ст.пр. Киреева М.М. | каб. 304 | / 1н
                    Макроэкономика | доц. Голышева Е.Е. | каб. 304 | / 2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 – 13:55] – Английский язык | доц. Смирнова А.Н. | каб. 304 | / 1н
                    Теория организации | ст.пр. Киреева М.М. | каб.304 | / 2н
                </Text>
            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="m1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Pt;