import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const PtE = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Пятница(1Э)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤  1 Пара | [8:30 - 10:05] – Эл. Дис. по физической культуре и спорту | Калмыков К.Б. | спорт зал | / 1н
                    Право | ст.пр. Пантелеев О.В. | каб. 303 | / 2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 - 11:50] - Линейная алгебра | доц. Анисимова И.В. | каб. 303 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 - 13:55] - Информационные технологии и системы в сфере экономики | проф. Миролюбова А.А. | каб. 508 |
                </Text>
            </Div>
        </Group>
        <Group header={<Header mode="secondary"> </Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="e1">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>
);


export default PtE;