import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';



const Pin1H2 = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>1ПИН[2 неделя]</PanelHeader>

        <Group header={<Header mode="secondary"></Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pn2h">
                    Понедельник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="bt2h">
                    Вторник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="sr2h">
                    Среда
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="ht2h">
                    Четверг
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pt2h">
                    Пятница
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default Pin1H2;
