import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

import '../../Persik.css';

const Pin1 = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>1Э</PanelHeader>

        <Group header={<Header mode="secondary"></Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pne">
                    Понедельник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="bte">
                    Вторник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="sre">
                    Среда
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="hte">
                    Четверг
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pte">
                    Пятница
                </Button>
            </Div>

            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="groupO">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default Pin1;
