import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

import '../../Persik.css';

const Eb1 = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>1ЭБ</PanelHeader>

        <Group header={<Header mode="secondary"></Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pned">
                    Понедельник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="bted">
                    Вторник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="sred">
                    Среда
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="hteb">
                    Четверг
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pted">
                    Пятница
                </Button>
            </Div>

            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="groupO">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default Eb1;
