import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, Header, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';

import '../../Persik.css';

const M1 = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>1M</PanelHeader>

        <Group header={<Header mode="secondary"></Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pnm">
                    Понедельник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="btm">
                    Вторник
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="srm">
                    Среда
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="htm">
                    Четверг
                </Button>
            </Div>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="ptm">
                    Пятница
                </Button>
            </Div>

            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="groupO">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>



);



export default M1;
