import React, { useState, useEffect, Component  } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, ScreenSpinner, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import 'bootstrap/dist/css/bootstrap.css'


import Home from './panels/Home';
import Persik from './panels/Persik';
import KursO from './panels/Kurs/KursO';
import KursZa from './panels/Kurs/KursZa';
import GroupO from './panels/Group/GroupO_ZA/GroupO';
import Pin1 from './panels/Group/Pin1/Pin1';
import Pn from './panels/Group/Pin1/Deys/Pn';
import Bt from './panels/Group/Pin1/Deys/Bt';
import Sr from './panels/Group/Pin1/Deys/Sr';
import Ht from './panels/Group/Pin1/Deys/Ht';
import Pt from './panels/Group/Pin1/Deys/Pt';
import Pn2H from "./panels/Group/Pin1/Deys/Deys2H/Pn2H";
import Bt2H from "./panels/Group/Pin1/Deys/Deys2H/Bt2H";
import Sr2H from "./panels/Group/Pin1/Deys/Deys2H/Sr2H";
import Ht2H from "./panels/Group/Pin1/Deys/Deys2H/Ht2H";
import Pt2H from "./panels/Group/Pin1/Deys/Deys2H/Pt2H";
import Pin1H2 from "./panels/Group/Pin1/Deys/Deys2H/Pin1H2/Pin1H2";
import M1 from "./panels/Group/1M/M1";
import PnM from "./panels/Group/1M/Day/PnM";
import BtM from "./panels/Group/1M/Day/BtM";
import SrM from "./panels/Group/1M/Day/SrM";
import HtM from "./panels/Group/1M/Day/HtM";
import PtM from "./panels/Group/1M/Day/PtM";
import E1 from "./panels/Group/1Э/E1";
import PnE from "./panels/Group/1Э/Day/PnE";
import BtE from "./panels/Group/1Э/Day/BtE";
import SrE from "./panels/Group/1Э/Day/SrE";
import HtE from "./panels/Group/1Э/Day/HtE";
import PtE from "./panels/Group/1Э/Day/PtE";
import Eb1 from "./panels/Group/1Eb/Eb1";
import PnEb from "./panels/Group/1Eb/Day/PnEb";
import BtEb from "./panels/Group/1Eb/Day/BtEb";
import SrEb from "./panels/Group/1Eb/Day/SrEb";
import HtEb from "./panels/Group/1Eb/Day/HtEb";
import PtEb from "./panels/Group/1Eb/Day/PtEb";
import Sb from "./panels/Group/1M/Day/Sb";



const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);



	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' fetchedUser={fetchedUser} go={go} />
					<Persik id='persik' go={go} />
					<KursO id='kurso' go={go} />
					<KursZa id='kursZa' go={go}/>
					<GroupO id='groupO' go={go}/>
					<Pin1 id = 'pin1' go={go}/>
					<Pn id = 'pn' go={go}/>
					<Bt id = 'bt' go={go}/>
					<Sr id = 'sr' go={go}/>
					<Ht id = 'ht' go={go}/>
					<Pt id = 'pt' go={go}/>
					<Pn2H id = 'pn2h' go={go}/>
					<Bt2H id = 'bt2h' go={go}/>
					<Sr2H id = 'sr2h' go={go}/>
					<Ht2H id = 'ht2h' go={go}/>
					<Pt2H id = 'pt2h' go={go}/>
					<Pin1H2 id = 'pinh2' go={go}/>
					<M1 id='m1' go={go}/>
					<PnM id = 'pnm' go={go}/>
					<BtM id = 'btm' go={go}/>
					<SrM id = 'srm' go={go}/>
					<HtM id = 'htm' go={go}/>
					<PtM id = 'ptm' go={go}/>
					<E1 id = 'e1' go={go}/>
					<PnE id = 'pne' go={go}/>
					<BtE id='bte' go={go}/>
					<SrE id ='sre' go={go}/>
					<HtE id = 'hte' go={go}/>
					<PtE id = 'pte' go={go}/>
					<Eb1 id = 'eb1' go={go}/>
					<PnEb id = 'pned' go={go}/>
					<BtEb id = 'bted' go={go}/>
					<SrEb id = 'sred' go={go}/>
					<HtEb id = 'hteb' go={go}/>
					<PtEb id = 'pted' go={go}/>
					<Sb id = "sb" go = {go}/>
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}


export default App;
