import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text, CardGrid, ContentCard,} from '@vkontakte/vkui';


const PnEb = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Понедельник(1ЭБ)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 Пара | [8:30 - 10:05] – Эл. Дис. по физической культуре и спорту | Калмыков К.Б. | спорт зал / 1н |
                    Физическая культура | Калмыков К.Б. | спорт зал | / 2н
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 - 11:50] - Культура речи и деловое общение | доц. Николаева О.А. | каб. 414 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 - 13:55] - Культура речи и деловое общение | доц. Николаева О.А. | каб. 414 |
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="eb1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default PnEb;