import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Sr = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Среда(1M)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 пара | [8:30 - 10:05] – Право | ст.пр. Пантелеев О.В. | дистант
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 – 11:50] – Физическая культура | Калмыков. К.Б. | дистант
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 – 13:55] – Маркетинг | доц. Вострова А.П. | дистант |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 4 Пара | [14:25 - 16:00] – Маркетинг | доц. Вострова А.П. | дистант | / 1н
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="m1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Sr;