import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text, Link,ContentCard, TabsItem} from '@vkontakte/vkui';




const Sb = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Вторник(1ПИН)[2 неделя]</PanelHeader>
        <Group>
            <Div>
                <img src="https://i.imgur.com/ud4pstM.png"/>
            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="pinh2">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>
);

export default Sb;