import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Ht = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Четверг(1ПИН)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 Пара | [8:30 - 10:05] – Основы объектно-ориентированного программирования | доц. Гнатюк А.Б. | каб. 401 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 – 11:50] – Дискретная математик | доц. Туртин Д.В. | каб. 307 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 – 13:55] – Тайм-менеджмент | ст.пр. Киреева М.М. | каб. 307 |/ 1н |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 Пара | [12:20 – 13:55] – Право | ст.пр. Пантелеев О.В. | каб. 307 | / 2н
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="pin1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Ht;