import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const HtEb = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Четверг(1ЭБ)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 Пара | [8:30 - 10:05] – Эл.  Дис. по физической культуре и спорту | Калмыков К.Б. | спортзал |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 - 11:50] - Линейная алгебра | доц. Анисимова И.В. | каб. 410 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 3 пара | [12:20 - 13:55] - Математический анализ | доц. Туртин Д.В. | каб. 410 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 4 Пара | [14:25 - 16:00] - Теория государства и права | доц. Макарова И.В. | каб. 410 |
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>

            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="eb1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default HtEb;