import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text} from '@vkontakte/vkui';




const Ht = ({id, go}) => (
    <Panel id={id}>
        <PanelHeader>Четверг(1M)</PanelHeader>


        <Group>
            <Div>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 1 Пара | [8:30 - 10:05] – Право | ст.пр. Пантелеев О.В. | каб. 304 |
                </Text>
                <Text weight="medium" style={{ marginBottom: 16 }}>
                    ➤ 2 Пара | [10:15 – 11:50] –  Элективные дисциплины по физической культуре и спорту | Калмыков К.Б. | спортзал |
                </Text>

            </Div>
        </Group>


        <Group header={<Header mode="secondary"> </Header>}>



            <Div>

                <Button stretched size="l" mode="secondary" onClick={go} data-to="m1">
                    Назад
                </Button>

            </Div>

        </Group>
    </Panel>
);


export default Ht;